
import React , {useEffect, useState, useContext, useRef} from "react";
import { Redirect} from "react-router";
import { AuthContext } from "../auth/Auth";
import app from "../auth/base";

const Header = (props) => {
    const [profileUrl, setProfileUrl] = useState("") 
    const [userDtls, setUsrDtls] = useState({});
    const {currentUser} = useContext(AuthContext);
    useEffect(()=>{        
        const GetRef = app.database().ref('users/'+currentUser.uid+"/personalinfo");
        GetRef.on("value", (snap)=>{
            setUsrDtls(snap.val())
        });
        app.database().ref('profile-img/'+currentUser.uid).on("value", (purl)=>{
            setProfileUrl(purl.val());
        });
        
    }, [])    
  

    return(
        <>
            <section className="siteHeader">
                <div className="d-flex justify-content-between align-items-center">
                <a href="/" className="logo">
                    Logo
                </a> 
                <div className="d-flex align-items-center">
                    <div className="userOption">
                        <ul>
                            <li><a href="/"><i className="fa fa-newspaper-o"></i>Feed</a></li>
                            <li><a href="/friend-requests"><i className="fa fa-user"></i>Friends</a></li>
                            <li><a href="/chat"><i className="fa fa-comments"></i>Chat</a></li>                            
                        </ul>
                    </div>
                    <a href={'/users/'+currentUser.uid} className="header_userInfo" >
                        <div className="headerUserImg">
                            {(() => {
                                if (profileUrl && profileUrl != "") {
                                    return <img src={profileUrl} />;                                
                                } else{
                                    return <img src={require("../../assets/images/no-profile.png")} />;
                                }
                            })()}
                        </div>
                        <div className="headerUserName">
                            {userDtls.first_name} {userDtls.Last_name}
                        </div>
                    </a>
                    <button className="signOutBtn" onClick={() => app.auth().signOut()}>Sign out</button>
                </div> 
                </div>  
            </section>
        </>
    );

}

export default Header;
