import React, { useCallback, useContext, useState } from "react";
import { withRouter, Redirect } from "react-router";
import app from "./base.js";
import { AuthContext } from "../auth/Auth";

const Login = ({ history },) => {
  const [loaderStatus, setLoaderVisible ] = useState("none");

  function googleLogin(){
    


  } 
  
  const handleLogin = useCallback(
    
    async event => {
      event.preventDefault();
      setLoaderVisible("block");
      const { email, password } = event.target.elements;
      try {
        await app
          .auth()
          .signInWithEmailAndPassword(email.value, password.value);
        history.push("/");
      } catch (error) {
        alert(error);
      }
    },

    [history]
  );

  const { currentUser } = useContext(AuthContext);

  if (currentUser) {
    return <Redirect to="/" />;
  }


  return (
      <section className="authSection">
        <div className="authInnerSect">
            <div className="login-box">
                <div className="logIngLogo">
                    <img src={require('../../assets/images/music-note.svg')} alt="" width="80"/> 
                    <h1>Music Gallery</h1>               
                </div>
                
                <form className="email-login"  onSubmit={handleLogin}>
                    <div className="u-form-group">
                        <input name="email" type="email" placeholder="Email" id="logInEmail"/>
                    </div>
                    <div className="u-form-group text-right">
                        <input  name="password" type="password" placeholder="Password" id="logInPass"/>
                        <a href="#" className="forgot-password">Forgot password?</a>
                    </div>
                    <div className="u-form-group">
                        <button type="submit" className="logInButtonar">Log in</button>
                    </div>
                    <div className="u-form-group extraLoginBoxlink">
                        <p>Did not have account <a href="/signup">Sign Up Now</a> </p>  
                    </div>
                    <div className="loaderArInfo" style={{"display": loaderStatus}}>
                      <div className="spinnerWrapper">
                        <i className="fa fa-spinner fa-spin"></i>
                      </div>
                    </div>
                </form>
            </div>
        </div>
  </section>

  );
};

export default withRouter(Login);
