import React, { useCallback, useContext, useState } from "react";
import { withRouter, Redirect, Switch, Link  } from "react-router";
import app from "./base";
import { AuthContext } from "../auth/Auth";
import "./signUp.css";

const SignUp = ({ history }) => {
  const [loaderStatus, setLoaderVisible ] = useState("none");

  const handleSignUp = useCallback(async event => {
    event.preventDefault();
    setLoaderVisible("block");
    const timsmp =Date.now();
    const { email, password, firstname, Lastname, phone, age, gender } = event.target.elements;
    try {
      const userAuth = await app
        .auth()
        .createUserWithEmailAndPassword(email.value, password.value)
        .then((data)=>{
          console.log(data);
          let user = {                      
            uid: data.user.uid,
            // first_name: firstname.value,
            // Last_name: Lastname.value,
            email: email.value,
            // phone: phone.value,
            // dob: age.value,
            // gender: gender.value,
            joined: timsmp, 
          }
          writeUserData(user);
        })
        
     
      function writeUserData(user) {
        app.database().ref('users/' + user.uid+"/personalinfo").set(user).then(()=>{
          history.push("/");
        }).catch(error => {
            console.log(error.message)
        });
      }

      
    } catch (error) {
      alert(error);
    }
  }, [history]);



  
  const { currentUser } = useContext(AuthContext);

  if (currentUser) {
    return <Redirect to="/" />;
  }

  return (    
    <section className="authSection">
      <div className="authInnerSect">
          <div className="login-box">
              <div className="logIngLogo">
                  <img src={require('../../assets/images/music-note.svg')} alt="" width="80"/> 
                  <h1>Music Gallery</h1>               
              </div>            
              <form onSubmit={handleSignUp} className="email-signup">
                  <div className="u-form-group">
                      <input name="email" type="email" placeholder="Email" id="regEmail" />
                  </div>
                  <div className="u-form-group">
                      <input name="password" type="password" placeholder="Password" id="regPass"/>
                  </div>
                  <div className="u-form-group">
                      <input name="confirm-password" type="password" placeholder="Confirm Password" id="regConfirm"/>
                  </div>
                  <div className="u-form-group">
                      <button type="submit" className="logInButtonar">Sign Up</button>
                  </div>
                  <div className="u-form-group extraLoginBoxlink">
                      <p>Already have an Account <a href="/login">Login</a></p>                    
                  </div>
                  <div className="loaderArInfo" style={{"display": loaderStatus}}>
                    <div className="spinnerWrapper">
                      <i className="fa fa-spinner fa-spin"></i>
                    </div>
                  </div>
              </form>
            
      
          </div>

      </div>
  </section>



  );
};

export default withRouter(SignUp);
