import React , {useEffect, useState} from "react";
import app from "../auth/base";
import '../../assets/css/feed.css';
import './chat.css';
const Friends = (prms) => {

    let [friendDtls, setFriends] = useState({});

    const db = app.database();
    let activeMark = prms.active;   
    useEffect(()=>{
        let tempObj={};
        db.ref("/users/"+prms.dtls).on("value", (e)=>{        
            tempObj = e.val()
            tempObj.id =  prms.dtls;  
            setFriends(tempObj);         
        }); 
    }, []);    

return(<>
        <a href={"/chat/"+friendDtls.id} className={activeMark? "contact active" : "contact" } contact-uid="7v07Kecx8ZcWM8NRqhCkod4JsiC2">
            <div className="pic">
                {(() => {
                    if (friendDtls.profileUrl && friendDtls.profileUrl != "") {
                        return <img src={friendDtls.profileUrl} />;                                
                    } else{
                        return <img src={require("../../assets/images/no-profile.png")} />;
                    }
                })()}                               
            </div>
            <div>
                <div className="name">
                    {friendDtls.name}
                </div>
                <div className="message" id="lastMsg">
                    ...
                </div>
            </div>
        </a>       
      </>);

}

export default Friends;
