import React , {useEffect, useState, useContext} from "react";
import { AuthContext } from "../auth/Auth";
import app from "../auth/base";

import Header from '../laouts/header'


import  "../../assets/css/profile.css";
const Profile = ({match}) => {
    const { currentUser } = useContext(AuthContext);
    
    const [usrDtls, setUserDtls] = useState({})
    useEffect(()=>{
        const userDtsRef = app.database().ref('users/'+match.params.id+"/personalinfo");
        userDtsRef.on("value", (snp)=>{
            setUserDtls(snp.val());
        });
        
    }, []);       

    return(
        <>
            <Header/>
            <section className="profileWrapper">
                <div className="profileLeft">
                    <div className="profileIdentity">
                        <div className="profileImg">
                            {(() => {
                                if (usrDtls.profileUrl && usrDtls.profileUrl != "") {
                                    return <img src={usrDtls.profileUrl} />;                                
                                } else{
                                    return <img src={require("../../assets/images/no-profile.png")} />;
                                }
                            })()}
                        </div>
                        {usrDtls.first_name && <h4>{usrDtls.first_name} {usrDtls.Last_name} {currentUser.uid == match.params.id && <a className="editIcon" href={"/edit/"+currentUser.uid}> <i className="fa fa-edit"></i></a>}</h4>}
                    </div>
                    <div className="about_auth">
                        <ul>
                            {usrDtls.education &&<li><i className="fa fa-graduation-cap"></i> {usrDtls.education}</li>}
                            {usrDtls.phone && <li><i className="fa fa-phone"></i> Phone <b>{usrDtls.phone}</b></li>}
                            {usrDtls.gender && <li><i className="fa fa-user"></i> Gender <b>{usrDtls.gender}</b></li>}
                            
                            {usrDtls.dob && <li><i className="fa fa-user"></i> Age <b>{usrDtls.age}</b></li>}
                            {usrDtls.joined && <li><i className="fa fa-clock-o"></i> Joined <b>{usrDtls.joined}</b></li>}                            
                        </ul>
                    </div>
                    {currentUser.uid !== match.params.id && <><div className="addRquestBtn"><a href="#"><i className="fa fa-user-plus"></i>Add to Friend</a></div><div className="sendMessage"><a href="#"><i className="fa fa-comments"></i>Message</a></div></> }

                    {currentUser.uid == match.params.id && <div className="addRquestBtn"><a href={"/edit/"+currentUser.uid}><i className="fa fa-pencil"></i>Edit</a></div> }
                </div> 
                <div className="profileRight">
                    <div className="tabOptions">
                        <ul>
                            <li><a className="active">Feed</a></li>
                            <li><a >Photos</a></li>
                            <li><a >Friends</a></li>
                        </ul>
                    </div>
                    <div className="EachFeedBox">
                        <div className="authorDetails">
                            <div className="authImg">
                                <a href={"/users/"}>
                                    <img src={require('../../assets/images/no-profile.png')} />
                                </a>
                            </div>
                            <div className="authDetails">
                                <h4><a href={"/users/"}>sdfsdfsdfsdf</a></h4>
                                <p>8hrs ago</p>
                            </div> 
                        </div> 
                        <p>Loremsdf sdfsdfsdfsdfsdf</p>
                    </div>
                    <div className="EachFeedBox">
                        <div className="authorDetails">
                            <div className="authImg">
                                <a href={"/users/"}>
                                    <img src={require('../../assets/images/no-profile.png')} />
                                </a>
                            </div>
                            <div className="authDetails">
                                <h4><a href={"/users/"}>sdfsdfsdfsdf</a></h4>
                                <p>8hrs ago</p>
                            </div> 
                        </div> 
                        <p>Loremsdf sdfsdfsdfsdfsdf</p>
                    </div>
                    <div className="EachFeedBox">
                        <div className="authorDetails">
                            <div className="authImg">
                                <a href={"/users/"}>
                                    <img src={require('../../assets/images/no-profile.png')} />
                                </a>
                            </div>
                            <div className="authDetails">
                                <h4><a href={"/users/"}>sdfsdfsdfsdf</a></h4>
                                <p>8hrs ago</p>
                            </div> 
                        </div> 
                        <p>Loremsdf sdfsdfsdfsdfsdf</p>
                    </div>
                    <div className="EachFeedBox">
                        <div className="authorDetails">
                            <div className="authImg">
                                <a href={"/users/"}>
                                    <img src={require('../../assets/images/no-profile.png')} />
                                </a>
                            </div>
                            <div className="authDetails">
                                <h4><a href={"/users/"}>sdfsdfsdfsdf</a></h4>
                                <p>8hrs ago</p>
                            </div> 
                        </div> 
                        <p>Loremsdf sdfsdfsdfsdfsdf</p>
                    </div>
                    <div className="EachFeedBox">
                        <div className="authorDetails">
                            <div className="authImg">
                                <a href={"/users/"}>
                                    <img src={require('../../assets/images/no-profile.png')} />
                                </a>
                            </div>
                            <div className="authDetails">
                                <h4><a href={"/users/"}>sdfsdfsdfsdf</a></h4>
                                <p>8hrs ago</p>
                            </div> 
                        </div> 
                        <p>Loremsdf sdfsdfsdfsdfsdf</p>
                    </div>
                    <div className="EachFeedBox">
                        <div className="authorDetails">
                            <div className="authImg">
                                <a href={"/users/"}>
                                    <img src={require('../../assets/images/no-profile.png')} />
                                </a>
                            </div>
                            <div className="authDetails">
                                <h4><a href={"/users/"}>sdfsdfsdfsdf</a></h4>
                                <p>8hrs ago</p>
                            </div> 
                        </div> 
                        <p>Loremsdf sdfsdfsdfsdfsdf</p>
                    </div>
                </div> 
            </section>
        </>
    )

}

export default Profile;
