import React , {useEffect, useState, useContext} from "react";
import { AuthContext } from "../auth/Auth";
import app from "../auth/base";
import Header from '../laouts/header';
import  "../../assets/css/profile.css";
import  "./edit.css";

const Edit = ({match}) => {
    const { currentUser } = useContext(AuthContext);
    const [profileUrl, setProfileUrl] = useState("")
    const [usrDtls, setUserDtls] = useState({});
    const inputFocusEvents=(prms)=>{
        let arclassList =  prms.target.parentElement.classList;
        arclassList.add("active");
        arclassList.add("hasValue");
    }
    const inputBlurEvents=(prms)=>{
        if(prms.target.value==""){
            prms.target.parentElement.classList.remove("hasValue");
        }
        prms.target.parentElement.classList.remove("active");
        
    }
    useEffect(()=>{
        app.database().ref('profile-img/'+match.params.id).on("value", (pUrr)=>{
            setProfileUrl(pUrr.val())
        })
        const userDtsRef = app.database().ref('users/'+match.params.id+"/personalinfo");
        userDtsRef.on("value", (snp)=>{
            setUserDtls(snp.val());
            console.log(usrDtls)
        });        
    }, []);

    const updateProfileImg = (e)=>{
        let file =e.files[0];
        var storageRef = app.storage().ref('/profile');
        // Create a reference to 'mountains.jpg'
        var mountainsRef = storageRef.child('profile.jpg');

        // Create a reference to 'images/mountains.jpg'
        var mountainImagesRef = storageRef.child('images/'+currentUser.uid);
        // Create file metadata including the content type
        var metadata = {
        contentType: 'image/jpeg',
        };
        var uploadTask = storageRef.child('images/'+currentUser.uid).put(file, metadata);

        uploadTask.on('state_changed', 
            (snapshot) => {
                // Observe state change events such as progress, pause, and resume
                // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
                var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                console.log('Upload is ' + progress + '% done');

                // switch (snapshot.state) {
                // case app.storage.TaskState.paused: // or 'paused'
                //     console.log('Upload is paused');
                //     break;
                // case app.storage.TaskState.running: // or 'running'
                //     console.log('Upload is running');
                //     break;
                // }
            }, 
            (error) => {

            }, 
            () => {                
                uploadTask.snapshot.ref.getDownloadURL().then((downloadURL) => {
                console.log('File available at', downloadURL);
                    // console.log(usrDtls)
                   
                    app.database().ref("/profile-img/"+match.params.id).set(downloadURL);

                });
            }
            );




        // console.log(storageRef);
        // console.log()
    }
    

    return(
        <>
            <Header/>
            <section className="profileWrapper">
                <div className="profileLeft">
                    <div className="profileIdentity">
                        <div className="profileImg">
                            {(() => {
                                if (profileUrl && profileUrl != "") {
                                    return <img src={profileUrl} />;                                
                                } else{
                                    return <img src={require("../../assets/images/no-profile.png")} />;
                                }
                            })()}                            
                        </div>

                        <h4>{usrDtls.first_name} {usrDtls.Last_name}</h4>
                        
                    </div>
                    <div className="about_auth">
                        <ul>
                        {usrDtls.education &&<li><i className="fa fa-graduation-cap"></i> {usrDtls.education}</li>}
                            {usrDtls.phone && <li><i className="fa fa-phone"></i> Phone <b>{usrDtls.phone}</b></li>}
                            {usrDtls.gender && <li><i className="fa fa-user"></i> Gender <b>{usrDtls.gender}</b></li>}                            
                            {usrDtls.dob && <li><i className="fa fa-user"></i> Age <b>{usrDtls.age}</b></li>}
                            {usrDtls.joined && <li><i className="fa fa-clock-o"></i> Joined <b>{usrDtls.joined}</b></li>} 
                        </ul>
                    </div>
                    <div className="addRquestBtn">
                        <a href="#"><i className="fa fa-user-plus"></i>Add to Friend</a>
                    </div> 
                    <div className="sendMessage">
                        <a href="#"><i className="fa fa-comments"></i>Message</a>
                    </div> 
                </div> 
                <div className="profileRight">
                    <div className="each_edit_info">
                        <h2>Edit Personal Info</h2>
                        <div className="row">
                            <div className="col-4">
                                <div class="profileImgAr" >
                                    {(() => {
                                        if (profileUrl && profileUrl != "") {
                                            return <img src={profileUrl} />;                                
                                        } else{
                                            return <img src={require("../../assets/images/no-profile.png")} />;
                                        }
                                    })()}

                                    <input type="file" className="profileInput" id="arProfile1" onChange={e=>updateProfileImg(e.target)}/>
                                    <label htmlFor="arProfile1" className="changeProfileImg">
                                        <i className="fa fa-upload"></i> change profile picture
                                    </label>
                                </div>
                            </div>
                            <div className="col-8">
                            <div className="row">
                            <div className="col-6 form-group">  
                                <div className={usrDtls.first_name ? "profileInputWrp hasValue" : "profileInputWrp"}>                            
                                    <label className="arLabel1">First Name</label>                          
                                    <input className="arInput2" onFocus={(e)=>{inputFocusEvents(e)}} onBlur={(e)=>{inputBlurEvents(e)}} value={usrDtls.first_name}/>
                                </div>
                            </div>
                            <div className="col-6 form-group">  
                                <div className={usrDtls.Last_name ? "profileInputWrp hasValue" : "profileInputWrp"}>                            
                                    <label className="arLabel1">Last Name</label>                          
                                    <input className="arInput2" onFocus={(e)=>{inputFocusEvents(e)}} onBlur={(e)=>{inputBlurEvents(e)}} value={usrDtls.Last_name}/>
                                </div>
                            </div>
                            <div className="col-6 form-group">  
                                <div className="profileInputWrp noMoventForDb">                            
                                    <label className="arLabel1">Enter DOB</label>                          
                                    <input type="date" className="arInput2" onFocus={(e)=>{inputFocusEvents(e)}} onBlur={(e)=>{inputBlurEvents(e)}} value={usrDtls.dob}/>
                                </div>
                            </div> 
                            <div className="col-6 form-group">  
                                <div className={usrDtls.phone ? "profileInputWrp hasValue" : "profileInputWrp"}>                            
                                    <label className="arLabel1">Phone No</label>                          
                                    <input type="text" className="arInput2" onFocus={(e)=>{inputFocusEvents(e)}} onBlur={(e)=>{inputBlurEvents(e)}} value={usrDtls.phone}/>
                                </div>
                            </div> 
                            <div className="col-6 form-group">  
                                <div className={usrDtls.email ? "profileInputWrp hasValue" : "profileInputWrp"}>                            
                                    <label className="arLabel1">Email Address</label>                          
                                    <input type="text" className="arInput2" onFocus={(e)=>{inputFocusEvents(e)}} onBlur={(e)=>{inputBlurEvents(e)}} value={usrDtls.email}/>
                                </div>
                            </div>
                            <div className="col-6 form-group">  
                                <div className="profileInputWrp">                            
                                    <label className="arLabel1">Relationship</label>   
                                    <select className="arSelect2" onFocus={(e)=>{inputFocusEvents(e)}} onBlur={(e)=>{inputBlurEvents(e)}}>
                                        <option value=""></option>
                                        <option value="Single">Single</option>
                                        <option value="In a relationship">In a relationship</option>
                                        <option value="Engaged">Engaged</option>
                                        <option value="Married">Married</option>
                                        <option value="In a civil union">In a civil union</option>
                                        <option value="In a domestic partnership">In a domestic partnership</option>
                                        <option value="In an open relationship">In an open relationship</option>
                                        <option value="It's complicated">It's complicated</option>
                                        <option value="Separated">Separated</option>
                                        <option value="Divorced">Divorced</option>
                                        <option value="Widowed">Widowed</option>
                                        <option value="Live in relationship">Live in relationship</option>
                                    </select>
                                </div>
                            </div> 

                            <div className="col-6 form-group">
                                <h6 className="smallLavelHeading">Gender</h6>
                                <div className="groupOfRadio">
                                    {(() => {
                                        if (usrDtls.gender==="Male") {
                                            return(<>
                                                <div className="arEachRadio">
                                                    <input type="radio" className="arRadio1" id="genderid1" name="arRadio" checked/>
                                                    <label className="" for="genderid1">Male</label>   
                                                </div>
                                                <div className="arEachRadio">
                                                    <input type="radio" className="arRadio1" id="genderid2" name="arRadio"/>
                                                    <label className="" for="genderid2">Female</label>   
                                                </div>
                                                <div className="arEachRadio">
                                                    <input type="radio" className="arRadio1" id="genderid3" name="arRadio"/>
                                                    <label className="" for="genderid3">Others</label>   
                                                </div> 
                                                </>);                          
                                        } else if(usrDtls.gender==="Female"){
                                            return(<>
                                                <div className="arEachRadio">
                                                    <input type="radio" className="arRadio1" id="genderid1" name="arRadio"/>
                                                    <label className="" for="genderid1">Male</label>   
                                                </div>
                                                <div className="arEachRadio">
                                                    <input type="radio" className="arRadio1" id="genderid2" name="arRadio" checked/>
                                                    <label className="" for="genderid2">Female</label>   
                                                </div>
                                                <div className="arEachRadio">
                                                    <input type="radio" className="arRadio1" id="genderid3" name="arRadio"/>
                                                    <label className="" for="genderid3">Others</label>   
                                                </div> 
                                            </>); 
                                        }else if(usrDtls.gender==="Others"){
                                            return(<>
                                                <div className="arEachRadio">
                                                    <input type="radio" className="arRadio1" id="genderid1" name="arRadio"/>
                                                    <label className="" for="genderid1">Male</label>   
                                                </div>
                                                <div className="arEachRadio">
                                                    <input type="radio" className="arRadio1" id="genderid2" name="arRadio"/>
                                                    <label className="" for="genderid2">Female</label>   
                                                </div>
                                                <div className="arEachRadio">
                                                    <input type="radio" className="arRadio1" id="genderid3" name="arRadio" checked/>
                                                    <label className="" for="genderid3">Others</label>   
                                                </div> 
                                            </>); 
                                        }
                                    })()}
                                   
                                    
                                </div>                             
                            </div>
                            <div className="col-6 form-group text-right">
                                <button className="arUpdateButton">Update</button>
                            </div>
                            </div>
                            </div>
                            






                        </div>
                    </div>



                    <div className="each_edit_info mt-4">
                        <h2>Edit Others Info</h2>
                        <div className="row">
                            <div className="col-6 form-group">  
                                <div className="profileInputWrp">                            
                                    <label className="arLabel1">Job Description</label>                          
                                    <input className="arInput2" onFocus={(e)=>{inputFocusEvents(e)}} onBlur={(e)=>{inputBlurEvents(e)}}/>
                                </div>
                            </div>
                            <div className="col-6 form-group">  
                                <div className="profileInputWrp">                            
                                    <label className="arLabel1">Organization</label>                          
                                    <input className="arInput2" onFocus={(e)=>{inputFocusEvents(e)}} onBlur={(e)=>{inputBlurEvents(e)}}/>
                                </div>
                            </div>
                            <div className="col-6 form-group">  
                                <div className="profileInputWrp">                            
                                    <label className="arLabel1">Favourite Location</label>                          
                                    <input type="text" className="arInput2" onFocus={(e)=>{inputFocusEvents(e)}} onBlur={(e)=>{inputBlurEvents(e)}} placeholder=""/>
                                </div>
                            </div>  
                            <div className="col-6 form-group">  
                                <div className="profileInputWrp">                            
                                    <label className="arLabel1">Favourite Book</label>                          
                                    <input type="text" className="arInput2" onFocus={(e)=>{inputFocusEvents(e)}} onBlur={(e)=>{inputBlurEvents(e)}} placeholder=""/>
                                </div>
                            </div> 
                            <div className="col-6 form-group">  
                                <div className="profileInputWrp">                            
                                    <label className="arLabel1">Favourite Color</label>                          
                                    <input type="text" className="arInput2" onFocus={(e)=>{inputFocusEvents(e)}} onBlur={(e)=>{inputBlurEvents(e)}} placeholder=""/>
                                </div>
                            </div> 
                            <div className="col-6 form-group">  
                                <div className="profileInputWrp">                            
                                    <label className="arLabel1">Favourite Person</label>                          
                                    <input type="text" className="arInput2" onFocus={(e)=>{inputFocusEvents(e)}} onBlur={(e)=>{inputBlurEvents(e)}} placeholder=""/>
                                </div>
                            </div> 
                            <div className="col-6 form-group">  
                                <div className="profileInputWrp">                            
                                    <label className="arLabel1">Favourite Transport</label>                          
                                    <input type="text" className="arInput2" onFocus={(e)=>{inputFocusEvents(e)}} onBlur={(e)=>{inputBlurEvents(e)}} placeholder=""/>
                                </div>
                            </div> 
                            <div className="col-6 form-group">  
                                <div className="profileInputWrp">                            
                                    <label className="arLabel1">Favourite Food</label>                          
                                    <input type="text" className="arInput2" onFocus={(e)=>{inputFocusEvents(e)}} onBlur={(e)=>{inputBlurEvents(e)}} placeholder=""/>
                                </div>
                            </div> 
                            <div className="col-6 form-group">  
                                <div className="profileInputWrp">                            
                                    <label className="arLabel1">Favourite Actor</label>                          
                                    <input type="text" className="arInput2" onFocus={(e)=>{inputFocusEvents(e)}} onBlur={(e)=>{inputBlurEvents(e)}} placeholder=""/>
                                </div>
                            </div> 
                            <div className="col-6 form-group">  
                                <div className="profileInputWrp">                            
                                    <label className="arLabel1">Favourite Actress</label>                          
                                    <input type="text" className="arInput2" onFocus={(e)=>{inputFocusEvents(e)}} onBlur={(e)=>{inputBlurEvents(e)}} placeholder=""/>
                                </div>
                            </div>
                            <div className="col-6 form-group">  
                                <div className="profileInputWrp">                            
                                    <label className="arLabel1">Favourite Tv Show</label>                          
                                    <input type="text" className="arInput2" onFocus={(e)=>{inputFocusEvents(e)}} onBlur={(e)=>{inputBlurEvents(e)}} placeholder=""/>
                                </div>
                            </div> 
                            <div className="col-6 form-group">  
                                <div className="profileInputWrp">                            
                                    <label className="arLabel1">Favourite Movie</label>                          
                                    <input type="text" className="arInput2" onFocus={(e)=>{inputFocusEvents(e)}} onBlur={(e)=>{inputBlurEvents(e)}} placeholder=""/>
                                </div>
                            </div> 


                            <div className="col-12 text-left">
                                <button className="arUpdateButton">Update</button>
                            </div>


                        </div>
                    </div>
                    
                    


                    
                </div> 
            </section>
        </>
    )

}

export default Edit;
