import React , {useEffect, useState, useContext, useRef} from "react";
import app from "../../components/auth/base";
import { AuthContext } from "../../components/auth/Auth";
// import FeedBox from "./feed";
import Footer from '../../components/laouts/footer/footer'
import './home.css'
import { IoPlayOutline } from 'react-icons/io5'; 


const Home = () => {
  const {currentUser} = useContext(AuthContext);
  const [feed, setFeed] = useState('');
  const [allFeed, setAllfeed] = useState([]);
  // const [allFeed, setAllfeed] = useState([]);

  const inputRef = React.useRef();


  function readURL(input, imgChange) {
    if (input.files && input.files[0]) { 
    var reader = new FileReader();
    reader.onload = function (e) {
    imgChange.attr('src', e.target.result);
    }
    reader.readAsDataURL(input.files[0]);
    }
  }

  const postImage = (e)=>{
    let file =e.files[0];
    readURL(file)
    // console.log(file);
  }
  const feedPost = ()=>{
    var database = app.database();
    
    database.ref('posts/').push({
        aUid: currentUser.uid,
        acontent: feed,
        time: Date.now(),
      }).then(()=>{
        inputRef.current.value="";
        // console.log(inputRef)
        // inputRef
        alert("success");
      });
    
  }

  useEffect((previousList)=>{
    const commentsRef = app.database().ref('posts/');
    




  }, [])



  return (
    <>      
    <IoPlayOutline/>
      <section className="music_home_page">
        <div className="container">
          <div className="whats_on_your_mind">
            <h4>Write Something to Post: </h4>
            <textarea rows="5" className="feedTextArea" ref={inputRef} onChange={event => setFeed(event.target.value)}></textarea>
            <div className="imagesToGroup">
              <div className="imageWrapper">

              </div>
              <input type="file" id="homeUpload" hidden onChange={(e)=>{postImage(e.target)}}></input>
              <label className="addImages" htmlFor="homeUpload">
                <i className="fa fa-plus"></i>
              </label>
              
            </div>
            <div className="Submit_wrap">
              <button className="postButton" onClick={feedPost}>Post</button>
            </div>
          </div> 
          <div className="postedData">
              {/* {allFeed.map((index, key)=>(
                <FeedBox key={key} author={index.userId} content={index.postContent} time={index.time}/>                
              ))} */}


          </div>
          

        </div>
      </section>
      <Footer/>
    </>
  );
};

export default Home;
