import React, { useContext, useState } from "react";
import { Route, Redirect } from "react-router-dom";
import { AuthContext } from "../auth/Auth";
import app from "../auth/base";
import Header from '../laouts/header';
import "./friend.css";

const RequestList=()=>{
    const [getUser, setUsers] = useState([{}]);
    var db = app.database();
    const {currentUser} = useContext(AuthContext);

    const addToFriend = (e)=>{        
        let friendsRef = db.ref("users/"+e);
        let userVal = {};
        friendsRef.once("value", (snp)=>{
            userVal = snp.val();
        });
        let finalArr =[]
        finalArr = userVal["friends"] || [];    
        let x = finalArr.find((prms) => prms ===currentUser.uid);
        if(typeof x === "undefined"){            
            finalArr.push(currentUser.uid);
            
        }else{
            const index = finalArr.indexOf(currentUser.uid);
            if (index > -1) {
                finalArr.splice(index, 1);
            }
        }
        userVal["friends"] = finalArr; 
        friendsRef.update(userVal);

        //success upto here 


        let userVal2 = {};
        let friendsRef2 = db.ref("users/"+currentUser.uid);
        friendsRef2.once("value", (as)=>{
            userVal2 = as.val();
        });
        let finalArr2 =[]
        finalArr2 = userVal2["friends"] || [];    
        let x2 = finalArr2.find((prms) => prms ===e);
        if(typeof x2 === "undefined"){            
            finalArr2.push(e);
            
        }else{
            const index = finalArr2.indexOf(e);
            if (index > -1) {
                finalArr2.splice(index, 1);
            }
        }

        userVal2["friends"] = finalArr2; 
        friendsRef2.update(userVal2);

    }

    useState(()=>{
        let tolArr = [];
        db.ref("users/").on("value", (snap)=>{
            const snpVal = snap.val();
            for(let i in snpVal){
                let ab = snpVal[i];
                let b;
                if(typeof ab.friends!== "undefined"){
                    b = ab.friends.indexOf(currentUser.uid);
                    console.log(b, ab.friends)
                }                

                if(i!=currentUser.uid && typeof b === "undefined"){
                    let temp = snpVal[i];
                    temp.uid = i;                    
                    tolArr.push(temp);
                }
            } 
            setUsers(tolArr);        
        });
        
    }, []);


    return (
        <>
            <Header/> 
            <section className="listOfArry">
                <div className="container">
                    <div className="row">
                        {getUser.map((e, i)=>(
                            <div key={i} className="col-6">
                                <div className="each_friend_wrp">
                                    <div className="friendImg">
                                        {(() => {
                                            
                                            if (e.profileUrl && e.profileUrl != "")  {
                                                return <img src={e.profileUrl} />;                                
                                            } else{
                                                return <img src={require("../../assets/images/no-profile.png")} />;
                                            }
                                        })()}
                                    </div>
                                    <div className="friendDtls">
                                        <h4>{e.name}</h4>
                                        <p>age : {e.age}</p>

                                        <div className="addToFirend" onClick={()=>addToFriend(e.uid)} requestid={e.uid}>
                                            <i className="fa fa-user-plus"></i>
                                        </div>
                                    </div> 
                                </div>
                            </div> 
                        ))}                      
                     
                    </div> 
                </div>
            </section>
        </>
    );
}

export default RequestList;
